	module.exports = {"data":{"markdownRemark":{"html":"<p>Recently I have been spending some time to read a book called the <a href=\"https://www.amazon.com/Phoenix-Project-DevOps-Helping-Business/dp/0988262592\">The Phoenix Project</a>. It's a novel on the topic of IT and DevOps, and how the main character Bill Palmer has 90 days to rescue a company from disaster, otherwise the company would run into huge trouble and the whole IT department would be outsourced.</p>\n<img src=\"https://images-na.ssl-images-amazon.com/images/I/914-sUgELZL.jpg\" width=\"300\" height=\"400\">\n<p>The major takeaways for me are:</p>\n<ul>\n<li>Do not depend too much on a single person <a href=\"https://en.wikipedia.org/wiki/Bus_factor\">(bus factor)</a></li>\n<li>Plan for the worst <a href=\"https://en.wikipedia.org/wiki/Murphy%27s_law\">(Murphy's Law)</a></li>\n<li>Trust your team members</li>\n<li>Improve your processes</li>\n<li>Plan for the future</li>\n</ul>","frontmatter":{"title":"Phoenix Project Book Review"}}},"pathContext":{"slug":"/blog/05 - phoenix-project/"}}

